@import url(https://fonts.googleapis.com/css?family=Ubuntu);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f0f2f5;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.mb-1 {
  margin-bottom: 1em;
}
.mr-1 {
  margin-right: 1em;
}
.mt-2 {
  margin-top: 2em;
}
.ta-r {
  text-align: right;
}
form .help-block {
  color: red;
  font-size: .75em;
  text-align: right;
}
.user-details {
    display: flex;
    align-items: baseline;
}
.user-details h4 {
    color: #f1f1f1;
}
.dashboard {
    height: 100vh;
}
.dashboard .ant-layout-content {
    margin: 24px 16px 0;
}
.dashboard .ant-layout-content .content {
    height: 100vh;
    padding: 24px;
    background: '#fff';
    min-height: 360px;
}
.dashboard .ant-layout-sider .logo {
    display: flex;
    padding: 15px 10% 15px 0;
    flex: auto;
    align-items: center;
    flex-flow: column;
}

.dashboard .ant-layout-sider .logo img.logo-iso{
    height: 46px;
}
.dashboard .ant-layout-sider .logo img.logo-text{
    height: 46px;
    margin: 20px;
}

.dashboard .user-details {
    float: right;
}
.dashboard .ant-layout-header {
    padding: 0 10px;
}
.ant-menu.ant-menu-dark .ant-menu-item-selected,
.ant-menu-submenu-popup.ant-menu-dark .ant-menu-item-selected,
.ant-layout-sider-zero-width-trigger,
.ant-layout-sider-zero-width-trigger:hover {
    background: #fc6a42;
}
.dashboard .ant-menu-item svg{
    width: 1em!important;
    height: 1em!important;
}
.dashboard .header {
    display: flex;
    justify-content: space-between;
}
.dashboard .header .logo {
    display: block;
}
.dashboard .header {
    display: flex;
    justify-content: space-between;
}
/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {
    .dashboard .header {
        display: flex;
        justify-content: flex-end;
    }
    .dashboard .header .logo {
        display: none;
    }
} 

/* ZUBI STYLE */
body {
    background: #648cff;
}

  .ant-layout.zubi-app {
    background-color: transparent;
  }

  .ant-layout.zubi-app .ant-card {
      width: 335px;
      margin: 40px auto;
      padding: 10px;
      box-sizing: content-box;
  }
  .ant-layout.zubi-app .ant-card label {
      color: #999;
      font-family: Ubuntu;
  }
  .ant-btn {
    position: relative;
    margin-bottom: .3125rem;
    font-size: .875rem;
    text-decoration: none;
    text-transform: uppercase;
    letter-spacing: 0;
    cursor: pointer;
    border: 0;
    outline: 0;
    -webkit-transition: box-shadow .2s cubic-bezier(.4,0,1,1),background-color .2s cubic-bezier(.4,0,.2,1),color .2s cubic-bezier(.4,0,.2,1);
    transition: box-shadow .2s cubic-bezier(.4,0,1,1),background-color .2s cubic-bezier(.4,0,.2,1),color .2s cubic-bezier(.4,0,.2,1);
    will-change: box-shadow,transform;
    color: #fff;
    background-color: transparent;
    border-color: #ccc;
    border-radius: .125rem;
    font-family: Ubuntu;
    font-weight: 700;
  }
  a.ant-btn {
      line-height: 32px;
  }
  .ant-btn.ant-btn-primary {
    background-color: #fc6a42;
    color: #fff;
  }
  .ant-btn.ant-btn-default {
    color: #fc6a42;
    background-color: transparent;
  }
  .ant-btn.ant-btn-primary:hover {
    color: #fc6a42;
    background-color: transparent;
    border: none;
    padding: 0 15px;
  }
  .ant-btn.ant-btn-default:hover {
      color: #fff;
      background-color: #fc6a42;
  }


